.button-dark {
	width: 160px;
	padding-top: 12px;
	padding-bottom: 12px;
	border: 1px solid $prime-orange-color;
	border-radius: 25px;
	background-color: transparent;

	font-weight: 600;
	font-size: 12px;
	line-height: 1.33;
	letter-spacing: 0.09em;
	text-transform: uppercase;

	color: $prime-white-color;
	cursor: pointer;

	transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

	&:hover,
	&:focus {
		background-color: $prime-orange-color;
	}
}

.button-light {
	width: 160px;
	padding-top: 12px;
	padding-bottom: 12px;
	border: 1px solid $prime-orange-color;
	border-radius: 25px;
	background-color: transparent;

	font-weight: 600;
	font-size: 12px;
	line-height: 1.33;
	letter-spacing: 0.09em;
	text-transform: uppercase;

	color: $prime-black-color;
	cursor: pointer;

	transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

	&:hover,
	&:focus {
		color: $prime-white-color;
		background-color: $prime-orange-color;
	}
}
