.price-section {
	background-color: #111319;
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;

	@media screen and (max-width: 767px) {
		background-image: url(./../images/price-section-png/price-mobile-1x-min.png);
		@media (min-resolution: 2dppx) {
			background-image: url(./../images/price-section-png/price-mobile-2x-min.png);
		}
	}
	@media screen and (min-width: 768px) and (max-width: 1199px) {
		background-image: url(./../images/price-section-png/price-tablet-1x-min.png);
		@media (min-resolution: 2dppx) {
			background-image: url(./../images/price-section-png/price-tablet-2x-min.png);
		}
	}
	@media screen and (min-width: 1200px) {
		background-image: url(./../images/price-section-png/price-desktop-1x-min.png);
		@media (min-resolution: 2dppx) {
			background-image: url(./../images/price-section-png/price-desktop-2x-min.png);
		}
	}
}

.price-subtitle {
	margin-bottom: 20px;

	font-weight: 600;
	font-size: 11px;
	line-height: 1.36;
	letter-spacing: 0.1em;
	text-transform: uppercase;
	color: rgba(157, 164, 189, 0.6);

	&:before {
		content: "";
		display: inline-block;
		vertical-align: middle;
		margin-bottom: 2px;
		width: 60px;
		height: 1px;
		margin-right: 20px;
		background-color: rgba(157, 164, 189, 0.6);
	}
}

.price-title {
	margin-bottom: 44px;

	font-family: Raleway, sans-serif;
	font-weight: 700;
	font-size: 42px;
	line-height: 1.17;
	letter-spacing: 0.05em;
	color: $prime-white-color;
}
.price-list {
	padding: 30px;
	margin-bottom: 50px;
	background-image: linear-gradient(180deg, #171a24 0%, rgba(23, 26, 36, 0.2) 100%);
	@media screen and (min-width: 768px) {
		padding: 30px 40px;
	}
	@media screen and (min-width: 1200px) {
		display: none;
	}
	&-desktop {
		display: flex;
	}
	&-part1 {
		width: 50%;
		padding: 30px 40px;
		margin-bottom: 50px;
		background-image: linear-gradient(180deg, #171a24 0%, rgba(23, 26, 36, 0.2) 100%);
		@media screen and (max-width: 1199px) {
			display: none;
		}
	}
	&-part2 {
		width: 50%;
		padding: 30px 40px;
		margin-bottom: 50px;
		background-image: linear-gradient(180deg, #090b13 0%, rgba(9, 11, 19, 0.2) 100%);
		@media screen and (max-width: 1199px) {
			display: none;
		}
	}
}

.price-list-item {
	display: flex;
	margin-bottom: 20px;
	font-weight: 400;
	font-size: 14px;
	line-height: 2.14;
	letter-spacing: 0.02em;
	color: $light-grey-color;

	@media screen and (min-width: 768px) {
		font-size: 18px;
	}
	&::before {
		content: "";
		border-bottom: 1px solid rgba(157, 164, 189, 0.12);
		flex-grow: 1;
		order: 2;
		margin: 0 6px 6px 6px;
	}
}

.price-list-value {
	order: 3;
}
.button-price {
	display: flex;
	margin: 0 auto;
	justify-content: center;
}
