a {
	text-decoration: none;
	color: inherit;
}
address {
	font-style: normal;
}
ul {
	list-style: none;
	padding: 0;
	margin: 0;
}
body {
	font-family: Open Sans, sans-serif;
	max-width: 1600px;
	margin-left: auto;
	margin-right: auto;
}
h1,
h2,
h3 {
	margin: 0;
}
p {
	margin: 0;
}
img {
	display: block;
	max-width: 100%;
}
button {
	padding: 0;
	border: none;
	font: inherit;
	color: inherit;
	background-color: transparent;
}
:focus {
	outline-color: $prime-orange-color;
}
